.Profile-container {
    background-color: #000000;
    color: white;
    min-height: 100vh;
    font-family: 'Orbitron', sans-serif;
  }
  
  .Profile-main {
    padding: 40px;
    max-width: 800px;
    margin: 0 auto;
  }
  
  .Profile-title {
    text-align: center;
    font-size: 4rem;
    margin-bottom: 40px;
  }
  
  .Profile-card {
    background-color: #151515;
    padding: 30px;
    border-radius: 16px;
    border: 1px solid #00CFFF;
    box-shadow: 0 0 10px #FF2EC6;
  }
  
  .Profile-username {
    font-size: 2rem;
    margin-bottom: 15px;
    color: #00CFFF;
  }
  
  .Profile-card p {
    margin: 10px 0;
    font-size: 1.1rem;
  }
  
  .Profile-card strong {
    color: #FF2EC6;
  }
  .Profile-button button {
    background-color: #00CFFF;
    color: black;
    font-weight: bold;
    padding: 5px 15px;
    border: none;
    border-radius: 10px;
    cursor: pointer;
    font-size: 1rem;
  }
  