
.Navbar-home-header {
    background-color: #111;
    padding: 20px 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
  }
  
  .NavBar-brand-title {
    font-family: 'Orbitron', sans-serif;
    font-weight: 600; /* Orbitron SemiBold */
    font-size: 2rem;
    letter-spacing: 2px;
    margin-right: 40px;
    margin-left:10px;
  }
  
  .NavBar-blue-text {
    color: #00CFFF; /* Neon Blue */
  }
  
  .NavBar-pink-text {
    color: #FF2EC6; /* Neon Pink */
  }
  
  .NavBar-nav-bar {
    display: flex;
    gap: 16px;
    flex-wrap: wrap;
  }
  
  .NavBar-nav-bar button {
    background-color: transparent;
    border: none;
    color: white;
    font-size: 1rem;
    cursor: pointer;
    padding: 8px 12px;
    border-radius: 8px;
    transition: background-color 0.2s, color 0.2s;
  }
  
  .NavBar-nav-bar button:hover {
    background-color: #FF2EC6;
    color: black;
  }
  
  .NavBar-user-info {
    display: flex;
    gap: 10px;
    text-align: center;
    color: white;
    font-weight: bold;
    margin-top: -15px;
    margin-right: 10px;
    justify-content: flex-end;
    font-size: 1rem;
  }

  .NavBar-logo{
      height: 50px;
      margin-left: 5px;
      vertical-align: middle;
  }