/* MyBets.css */
.my-bets-container {
  padding: 0px;
  background-color: #000;
  min-height: 100vh;
  font-family: 'Orbitron', sans-serif;
  font-weight: 600; /* Orbitron SemiBold */
}

.MyBets-page-title {
  text-align: center;
  font-size: 5rem;
  margin-bottom: 30px;
  font-family: 'Orbitron', sans-serif;
  font-weight: 600; /* Orbitron SemiBold */
}

.MyBets-Pink{
  color: #FF2EC6;
}

.MyBets-Blue{
  color: #00CFFF;
}

.MyBets-bet-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 24px;
  padding: 0 40px;
}

.MyBets-bet-card {
  background-color: #151515;
  border-radius: 12px;
  padding: 20px; 
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}


.MyBets-bet-card-blue {
  border: 1px solid #00CFFF;
}

.MyBets-bet-card-blue:hover {
  transform: translateY(-5px);
  box-shadow: 0 0 15px #00CFFF;
}


.MyBets-bet-card-pink {
  border: 1px solid #FF2EC6;
}

.MyBets-bet-card-pink:hover {
  transform: translateY(-5px);
  box-shadow: 0 0 15px #FF2EC6;
}

.MyBets-bet-match {
  font-size: 1.3rem;
  color: White;
  margin-bottom: 10px;
}

.MyBets-bet-amount {
  font-weight: bold;
  margin: 5px 0;
}


.MyBets-bet-card-blue .MyBets-bet-amount {
  color: #00CFFF;
}

.MyBets-bet-card-pink .MyBets-bet-amount {
  color: #FF2EC6;
}

.MyBets-bet-win {
  font-weight: bold;
  margin: 5px 0;
}


.MyBets-bet-card-blue .MyBets-bet-win {
  color: #FF2EC6;
}

.MyBets-bet-card-pink .MyBets-bet-win {
  color: #00CFFF;
}

.MyBets-bet-description {
  font-size: 0.95rem;
  color: White;
  margin-top: 10px;
  line-height: 1.4;
}


.MyBets-no-bets {
  text-align: center;
  color: white;
  background-color: #151515;
  border: 1px solid #333;
  border-radius: 12px;
  padding: 40px 20px;
  margin: 40px auto;
  max-width: 500px;
}

.Place-Bet{
  font-family: 'Orbitron', sans-serif;
  font-weight: 600; /* Orbitron SemiBold */
  background-color: #00CFFF;
  color: white;
  padding: 5px 20px;
  border: none;
  border-radius: 8px;
  font-size: 1.2rem;
  cursor:pointer;
  transition: background-color 0.3s ease;
}