.group-page-container {
  display: flex;
  min-height: 100vh;
  background-color: #0000;
  font-family: 'Orbitron', sans-serif;
  color: white;
}

.group-sidebar {
  width: 30%;
  background-color: #1E1E1E;
  padding: 2rem;
  border-right: 1px solid #333;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.group-main {
  flex: 1;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.group-input {
  padding: 0.8rem;
  width: 100%;
  border-radius: 8px;
  border: 1px solid #00CFFF;
  background-color: #1E1E1E;
  color: white;
  margin-top: 0.5rem;
  margin-bottom: 1rem;
}

.create-button {
  padding: 0.8rem;
  background-color: #00CFFF;
  color: white;
  font-family: 'Orbitron', sans-serif;
  font-weight: 600; /* Orbitron SemiBold */
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  cursor: pointer;
  width: 100%;
}

.create-button:hover {
  background-color: #FF2EC6;
}

.group-main ul {
  list-style: none;
  padding: 0;
}

.group-main li {
  padding: 0.8rem;
  margin-bottom: 0.5rem;
  background-color: #1E1E1E;
  border: 1px solid #333;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.group-main li:hover {
  background-color: #2c2c2c;
}

.group-details {
  background-color: #1E1E1E;
  padding: 2rem;
  border: 1px solid #333;
  border-radius: 16px;
}
.challenge-link {
  background-color: #00CFFF;
  color: white;
  padding: 8px 16px;
  margin: 5px 0;
  border: none;
  border-radius: 12px;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.challenge-link:hover {
  background-color: #0099bb; 
}

.group-members{
  background-color: #1E1E1E;
  padding: 2rem;
  border: 1px solid #333;
  border-radius: 16px;
}
